* {
    margin: 0;
    padding: 0;
}

ul {
    list-style: none;
    display: flex;
    position: fixed;
    bottom: 0;
    left: 0;
    height: 50px;
    line-height: 50px;
    width: 100%;
}

ul li {
    flex: 1;
    text-align: center;
    background-color: white;
}

.active {
    color: red;
}